.imageInRS{
    width: 100%;
}

.imageCover{
    float: left;
    position: absolute;
    width: 100%;
    overflow: hidden;
    background-color: rgba(17, 17, 17, 0.39);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2em;
    color: white;
    transition: height 1s;
}

.untoggled{
    height: 0;
}

.toggled{
    height: 100%;
}

.visitBtn{
    color: white!important;
}

.contentContainer{
    position: relative;
}